import React from "react";
import PtimeForm from "../components/PtimeForm";
import { connect } from "react-redux";
import { useNavigate } from "react-router-dom";
import { useSnackbar } from "notistack";
import { addnewParttime } from "../store/actions/CompanyActions";
const AddPtime = (props) => {
  const navigate = useNavigate();
  const { enqueueSnackbar } = useSnackbar();

  const handlePtimeAdd = async (params) => {
    if (params != null) {
      try {
        const data = { ...params, userId: props?.userData?.loginData?.user_id };
        //console.log(data,props?.userData?.loginData)
        const res = await props.addnewParttime(data);
        enqueueSnackbar("Parttime added Successful", { variant: "success" });
      } catch (err) {
        console.log(err);
        enqueueSnackbar(err, { variant: "error" });
      }
    }
  };
  return <PtimeForm navigate={navigate} handlePtimeAdd={handlePtimeAdd} />
};

function mapStateToProps(state) {
  return {
    userData: state.LoginReducer,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    addnewParttime: (params) => dispatch(addnewParttime(params)),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(AddPtime);
