
import { Field, Formik, Form } from "formik";
import { InputTextField } from "./InputTextField";
import * as Yup from "yup";
import MobileDatePicker from "./MobileDatePicker";
import {
  TabsContent,
} from "../common/components/ui/tabs"
import { Button } from "../common/components/ui/button"
import { Label } from "../common/components/ui/label"
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "../common/components/ui/card"
const ptimeForm = (props) => {
  const FormValidationSchema = Yup.object({
    companyName: Yup.string().required("Company name is required"),
    payRate: Yup.string().required("Pay rate is required"),
    startDate: Yup.string().required("Start date is required"),
  });

  return (
    <TabsContent value="all">
      <Card x-chunk="dashboard-06-chunk-0">
        <CardHeader>
          <CardTitle>{props.label}</CardTitle>
          <CardDescription>
            {props.label == "Add Shift" ? 'Please enter below details to add shift' : "Please edit below details to update shift"}
          </CardDescription>
        </CardHeader>
        <CardContent>
          <Formik
            initialValues={props.initialState}
            validationSchema={FormValidationSchema}
            onSubmit={(values, { setFieldValue }) => {
              props?.handleSubmit(values);
            }}
          >
            {({ values, setFieldValue, touched, errors }) => {
              return (
                <Form>
                  {/* <Grid
                    container
                    spacing={2}
                    mt={1}
                    sx={{
                      width: "100%",
                      border: "1px solid lightgrey",
                      borderRadius: "5px",
                      padding: "1rem 1rem 1rem 0",
                      marginLeft: "0",
                    }}
                  >
                    <Grid item md={12} xs={12} sx={{ marginTop: "-1rem" }}>
                      <Grid container spacing={2}>
                        <Grid item xs={12} md={12}>
                          <Field
                            name={`companyName`}
                            value={`${values.companyName}`}
                            label="Company Name"
                            placeholder="Company Name"
                            required
                            component={InputTextField}
                          //error={
                          //touchedItems?.apartmentUnitHouseNumber &&
                          //ADDRESS_DETAILS?.apartmentUnitHouseNumber
                          //}
                          />
                        </Grid>
                        <Grid item xs={12} md={12}>
                          <Field
                            name={`payRate`}
                            value={`${values.payRate}`}
                            required
                            label="Pay Rate"
                            placeholder="Pay Rate"
                            component={InputTextField}
                          />
                        </Grid>
                        <Grid item xs={12} md={12}>
                          <MobileDatePicker
                            views={["year", "month", "day"]}
                            inputFormat="MM/dd/yyyy"
                            name={`startDate`}
                            currvalue={`${values.startDate}`}
                            label="Joining Date"
                            setFormikFieldvalue={setFieldValue}
                            required={true}
                            endAdornmentRequired={true}
                            maxDate={new Date()}
                          />
                        </Grid>
                        <Grid
                          item
                          xs={12}
                          md={12}
                          sx={{ display: "flex", justifyContent: "center" }}
                        >
                          <Button
                            sx={{
                              width: "200px",
                              height: "39px",
                            }}
                            variant="contained"
                            type="submit"
                          >
                            {props.label}
                          </Button>
                        </Grid>
                      </Grid>
                    </Grid>
                  </Grid> */}
                  <div className="grid gap-6 ">
                    <div className="grid gap-3">
                      <Label >Company Name</Label>
                      <Field
                        name={`companyName`}
                        value={`${values.companyName}`}
                        label="Company Name"
                        placeholder="Company Name"
                        required
                        component={InputTextField}
                      //error={
                      //touchedItems?.apartmentUnitHouseNumber &&
                      //ADDRESS_DETAILS?.apartmentUnitHouseNumber
                      //}
                      />
                    </div>
                    <div className="grid gap-6 sm:grid-cols-2">
                      <div className="grid gap-3">
                        <Label >Company Name</Label>
                        <Field
                          name={`payRate`}
                          value={`${values.payRate}`}
                          required
                          label="Pay Rate"
                          placeholder="Pay Rate"
                          component={InputTextField}
                        />
                      </div>
                      <div className="grid gap-3">
                        <Label >Joining Date</Label>
                        <Field
                          name={`startDate`}
                          currvalue={`${values.startDate}`}
                          value={`${values.startDate}`}
                          required
                          setFormikFieldvalue={setFieldValue}
                          label="Pick the joining date"
                          component={MobileDatePicker}
                        />
                      </div></div>
                    <div className="flex flex-col items-center  text-center">
                      <Button className="mt-4" type="submit"> {props.label}</Button>
                    </div>
                  </div>
                </Form>
              );
            }}
          </Formik>
        </CardContent>
      </Card>
    </TabsContent>
  );
};

export default ptimeForm;
